import React, { Component } from 'react'
import { Badge, Button } from 'react-bootstrap';

class Tags extends Component {
    constructor(props) {
        super(props);
        this.state = {
            items: this.props.item.tags ? String(this.props.item.tags).split(',') : [],
            visibleCount: 5,
        };
    }

 
    handleShowMore = () => {
        this.setState(prevState => ({
            visibleCount: prevState.items.length,
        }));
    };

    handleShowLess = () => {
        this.setState({
            visibleCount: 5,
        });
    };
    render() {

        const { items, visibleCount } = this.state;
        const visibleItems = items.slice(0, visibleCount);
        return <>

            {visibleItems && visibleItems.length > 0 ? <div className="d-flex align-items-center flex-wrap mb-2 w-100 overflow-hidden border-bottom pb-2 mb-3">
                <div className='w-100 d-flex flex-wrap'>
                    {
                        visibleItems.map((Item, index) => {
                            return Item && String(Item).length > 0 ? <Badge key={index} bg='' className={`rounded-pill btn btn-outline-secondary ${index == 99999 ? 'border-gradient' : 'border'}`} onClick={this.props.onClickToPostTags(Item)}>{Item}</Badge> : null

                        })
                    }
                    {items.length > 5 ? visibleCount < items.length ? (
                        <Button variant='secondary' onClick={this.handleShowMore} className='rounded-pill px-3 py-0 h-26px fs-10 fst-italic m-1'>
                            Show More
                        </Button>
                    ) : (
                        <Button variant='secondary' onClick={this.handleShowLess} className='rounded-pill px-3 py-0 h-26px fs-10 fst-italic m-1'>
                            Show Less
                        </Button>
                    ):null}
                </div>
            </div> : null}
        </>
    }
}
export default Tags